/*Search box css*/
.search{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    top : -800px;
    left: 200px;
    color: #3D4E64;
}

.search input{
    height: 99px;
    width: 866px;
    border-radius: 50px;
    text-indent: 25px;
    border: 2px solid #d6d4d4;
